import React from "react";
import Container from "../../../components/container";
import OneQuestionForm from "../../../components/OneQuestionForm";
import { navigate } from "gatsby";
export default function Borracho() {
  return (
    <Container>
      <section className="flex-col md:flex-row flex items-center md:justify-between mt-8 mb-16 md:mb-12">
        <h1 className="text-6xl md:text-8xl font-bold tracking-tighter leading-tight md:pr-8 text-center">
          Hay que cobrar.
        </h1>
        <img
          className="mt-5 rounded-2xl "
          src="/bate.png"
          title="Bate y Pelota"
          alt="Bate y Pelota"
        />
        <OneQuestionForm
          question="Una pelota y un bate tienen un precio conjunto de 110 euros. El bate tiene un valor de 100 euros superior al de la pelota. ¿Qué precio tiene la pelota?"
          validateResponse={(input) => {
            let normalizedInput = input
              .normalize("NFKD")
              .replace(/[\u0300-\u036f]/g, "");
            return normalizedInput.toLowerCase().includes("5");
          }}
          onSuccess={() => navigate("/taza1/fin")}
        />
      </section>
    </Container>
  );
}
